___CSS_LOADER_EXPORT___.push([module.id, "\n.digivigilMainDiv{\n  display: block;\n  position: relative;\n\n  text-align: center;\n  color:  #205377;\n  overflow: visible;\n}\n.MainInfoDiv{\n  padding: 3px;\n  padding-top: 10px;\n  font-size: 18pt;\n  position: relative;\n  vertical-align: middle;\n  margin: 0;\n  text-align: left;\n  display: block;\n  background: rgba(255, 255, 255, 0.9);\n  padding: 20px;\n}\n#MainInfoAndLogo {\n  display: block;\n  margin: 0;\n  border: none;\n  padding: 0px;\n\n  padding-top : 10px;\n\n\n\n  margin-bottom: 40px;\n}\n#MainInfoDiv{\n  line-height: 18pt;\n  display: block;\n\n  text-align: left;\n\n  background: rgba(96, 95, 95, 0.3);\n  padding: 20px;\n\n  margin: auto;\n  font-size: 18pt;\n  white-space: normal;\n\n  display: inline-block;\n\n  text-align: center;\n  padding: 20px;\n  width: 100%;\n}\n.MainYouTubeDiv{\n\n  display: inline-block;\n  margin: auto;\n  margin-bottom: 40px;\n  margin-top: 20px;\n}\n.MainYoutubeVideoIframe{\n  display: block;\n  border: solid white 5px;\n  border-radius: 20px;\n  width: 80vw;\n  height: 45vw;\n}\n.MainRouterLink{\n  text-decoration: none;\n  font-weight: bold;\n  color: #205377\n}\n.MainRouterLink:hover{\n  color: #96170d\n}\n#digivigilMainTextLogo{\n  max-width: 30vw;\n}\n.mainLogoDiv {\n  position: relative;\n\n\n  display: inline-block;\n  background: rgba(255, 255, 255, 0.8);\n  margin: auto;\n  padding: 30px;\n  border-radius: 40px;\n  border: solid 5px #205377;\n  border: none;\n  height: auto;\n  vertical-align: top;\n\n  margin-bottom: 30px;\n}\n\n", "",{"version":3,"sources":["webpack://./src/components/digivigil/Main.vue"],"names":[],"mappings":";AA0DA;EACE,cAAc;EACd,kBAAkB;;EAElB,kBAAkB;EAClB,eAAe;EACf,iBAAiB;AAGnB;AAEA;EACE,YAAY;EACZ,iBAAiB;EACjB,eAAe;EACf,kBAAkB;EAClB,sBAAsB;EACtB,SAAS;EACT,gBAAgB;EAChB,cAAc;EACd,oCAAoC;EACpC,aAAa;AACf;AAEA;EACE,cAAc;EACd,SAAS;EACT,YAAY;EACZ,YAAY;;EAEZ,kBAAkB;;;;EAIlB,mBAAmB;AACrB;AACA;EACE,iBAAiB;EACjB,cAAc;;EAEd,gBAAgB;;EAEhB,iCAAiC;EACjC,aAAa;;EAEb,YAAY;EACZ,eAAe;EACf,mBAAmB;;EAEnB,qBAAqB;;EAErB,kBAAkB;EAClB,aAAa;EACb,WAAW;AACb;AAGA;;EAEE,qBAAqB;EACrB,YAAY;EACZ,mBAAmB;EACnB,gBAAgB;AAElB;AAGA;EACE,cAAc;EACd,uBAAuB;EACvB,mBAAmB;EACnB,WAAW;EACX,YAAY;AACd;AACA;EACE,qBAAqB;EACrB,iBAAiB;EACjB;AACF;AAEA;EACE;AACF;AAGA;EACE,eAAe;AAEjB;AAIA;EACE,kBAAkB;;;EAGlB,qBAAqB;EACrB,oCAAoC;EACpC,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,yBAAyB;EACzB,YAAY;EACZ,YAAY;EACZ,mBAAmB;;EAEnB,mBAAmB;AAErB","sourcesContent":["<template>\n  <div class=\"digivigilMainDiv\">\n    <div id =\"MainInfoAndLogo\">\n\n          <div class=\"mainLogoDiv\">\n            <img id=\"digivigilMainTextLogo\" :src=\"DigivigilTextLogo\"  alt=\"Digivigil\"/>\n          </div>\n          <br/>\n\n    </div>\n\n    <div class=\"MainInfoDiv\">\n\n      Welcome to Digivigil, a software development brand focused on creating high-quality solutions for the digital age. Currently, our team consists of a single developer who is dedicated to delivering the best possible user experience through our two exciting projects: Balek, a full-stack Javascript (Transitioning to TypeScript) framework, and Diaplode, a remote workstation web application built on Balek.\n      <br/>\n      <br/>\n      We understand the importance of collaboration and are always open to contributions from interested parties. Our mission is to create software that is highly accessible and can be used to solve real-world problems.\n      <br/>\n      <br/>\n      To achieve this goal, we are committed to taking several steps, including refactoring our codebase from JavaScript to TypeScript, updating our documentation, and creating comprehensive unit tests. We regularly update our progress on Github, where interested parties can follow our journey and contribute to our projects.\n      <br/>\n      <br/>\n      Thank you for your interest in Digivigil, and we look forward to sharing our progress with you.\n    </div>\n\n\n    <div class=\"MainYouTubeDiv\" style=\"margin-top: 75px;\">\n      <iframe class=\"MainYoutubeVideoIframe\"  src=\"https://www.youtube.com/embed/XO4WX0AjYLA\" title=\"YouTube video player\" frameborder=\"0\" allow=\"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture\" allowfullscreen=\"\"></iframe>\n\n    </div>\n\n\n\n\n  </div>\n</template>\n<script>\nimport digivigilLogo from './resources/digivigil.svg'\nimport DigivigilTextLogo from './resources/DigivigilLogoText.svg'\n\n\nimport BalekInfo from '../balek/Info.vue'\nimport DiaplodeInfo from '../diaplode/Info.vue'\n\nexport default {\n  name: \"Digivigil\",\n  data: function () {\n    return {\n      digivigilLogo: digivigilLogo,\n      DigivigilTextLogo: DigivigilTextLogo,\n    }\n  },\n  components: {\n    BalekInfo,DiaplodeInfo\n  }\n};\n</script>\n<style>\n.digivigilMainDiv{\n  display: block;\n  position: relative;\n\n  text-align: center;\n  color:  #205377;\n  overflow: visible;\n\n\n}\n\n.MainInfoDiv{\n  padding: 3px;\n  padding-top: 10px;\n  font-size: 18pt;\n  position: relative;\n  vertical-align: middle;\n  margin: 0;\n  text-align: left;\n  display: block;\n  background: rgba(255, 255, 255, 0.9);\n  padding: 20px;\n}\n\n#MainInfoAndLogo {\n  display: block;\n  margin: 0;\n  border: none;\n  padding: 0px;\n\n  padding-top : 10px;\n\n\n\n  margin-bottom: 40px;\n}\n#MainInfoDiv{\n  line-height: 18pt;\n  display: block;\n\n  text-align: left;\n\n  background: rgba(96, 95, 95, 0.3);\n  padding: 20px;\n\n  margin: auto;\n  font-size: 18pt;\n  white-space: normal;\n\n  display: inline-block;\n\n  text-align: center;\n  padding: 20px;\n  width: 100%;\n}\n\n\n.MainYouTubeDiv{\n\n  display: inline-block;\n  margin: auto;\n  margin-bottom: 40px;\n  margin-top: 20px;\n\n}\n\n\n.MainYoutubeVideoIframe{\n  display: block;\n  border: solid white 5px;\n  border-radius: 20px;\n  width: 80vw;\n  height: 45vw;\n}\n.MainRouterLink{\n  text-decoration: none;\n  font-weight: bold;\n  color: #205377\n}\n\n.MainRouterLink:hover{\n  color: #96170d\n}\n\n\n#digivigilMainTextLogo{\n  max-width: 30vw;\n\n}\n\n\n\n.mainLogoDiv {\n  position: relative;\n\n\n  display: inline-block;\n  background: rgba(255, 255, 255, 0.8);\n  margin: auto;\n  padding: 30px;\n  border-radius: 40px;\n  border: solid 5px #205377;\n  border: none;\n  height: auto;\n  vertical-align: top;\n\n  margin-bottom: 30px;\n\n}\n\n</style>"],"sourceRoot":""}]);
